import React, { Component } from 'react';
import { Col, Card, Table, Row, Form } from 'antd';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import {
  getPaperStudent,
  // getPapeType,
  savePaperType,
} from 'services/AdditionalAdmissionPapersPage';
import { BtnSave, BtnCancel } from 'components/common/button';
import { dateToString } from '../../../utils/formatUtil';

@Form.create({ name: 'FormSearchStudent' })
class PaperStudentDetail extends Component {
  state = {
    selectedRowKeys: [],
    dataTable: [],
    initID: [],
  };

  componentDidMount() {
    this.getDataPaperStudent();
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log('selectedRows------>', selectedRows);
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };
  getDataPaperStudent = () => {
    const studentId = this.props.match.params.id;
    getPaperStudent({ studentId }).then(res => {
      const listFinishedId = [];
      res.data.items.forEach(item => {
        if (item.isFinished === true) {
          listFinishedId.push(item.id);
        }
      });

      this.setState({
        dataTable: res.data.items,
        selectedRowKeys: listFinishedId,
        initID: listFinishedId,
      });
    });
  };

  savePaperType = async () => {
    const { dataTable, selectedRowKeys, initID } = this.state;

    const studentId = this.props.match.params.id;
    const dataUpdate = {
      // studentId,
      // userInputId: '3fa85f64-5717-4562-b3fc-2c963f66afa6',
      paperList: [],
    };
    initID.forEach(item => {
      const index = selectedRowKeys.indexOf(item);
      if (index !== -1) selectedRowKeys.splice(index, 1);
    });
    console.log('selectedRowKeys ------->', selectedRowKeys);
    console.log('initID', initID);
    selectedRowKeys.forEach(id => {
      dataTable.forEach(item => {
        if (item.id === id) {
          const dataPaper = {
            id: item.id,
            noOfPieces: item.noOfPieces,
            noOfPiecesReal: item.noOfPieces - item.noOfPiecesReal,
          };
          dataUpdate.paperList.push(dataPaper);
        }
      });
    });
    console.log('{ ...dataUpdate }', { ...dataUpdate });
    // await savePaperType(studentId);
    await savePaperType(studentId, { ...dataUpdate });

    this.props.history.goBack();
  };

  render() {
    const { selectedRowKeys, dataTable } = this.state;
    const columns = [
      {
        title: 'Mã giấy tờ',
        dataIndex: 'paperTypeId',
        key: 'paperTypeId',
      },
      {
        title: 'Tên giấy tờ',
        dataIndex: 'paperTypeLabel',
        key: 'paperTypeLabel',
      },
      {
        title: 'Số lượng đã nộp',
        dataIndex: 'noOfPiecesReal',
        key: 'noOfPiecesReal',
      },
      {
        title: 'Số lượng',
        dataIndex: 'noOfPieces',
        key: 'noOfPieces',
      },
      {
        title: 'Ngày nhận',
        dataIndex: 'receivedDate',
        key: 'receivedDate',
        render: (text, record) => <div>{dateToString(record.receivedDate)}</div>,
      },
    ];
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
      getCheckboxProps: record => ({
        disabled: record.isFinished === true, // Column configuration not to be checked
        name: record.isFinished,
      }),
    };
    return (
      <Card title="Thông tin giấy tờ">
        <Row>
          <Col span={24}>
            <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
              <Row gutter={24}>
                <Col span={24} style={{ textAlign: 'end' }}>
                  <BtnSave title="Nhận" onClick={this.savePaperType} />
                  <BtnCancel onClick={() => this.props.history.goBack()} />
                </Col>
              </Row>
            </Form>
          </Col>
          <Col span={24}>
            <Table
              rowSelection={rowSelection}
              columns={columns}
              rowKey="id"
              dataSource={dataTable}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <PaperStudentDetail {...props} />
  </Page>
));



